.menu-bar{
  background-color: red; 
  height: 20px;
  width: 100%;
  
  z-index:1;
  
  padding: 0px;
  left: 0px;
  
  
  position: fixed; 
  top: 0;
  
}

.content-container{
  display: flex;
  background: url(http://i.imgur.com/6Obm0Ek.png) repeat center center fixed;
  

  flex-direction: column;
  align-items: center;
  
  position: fixed;
  
  
  top: 0;
  left: 0;
  
  height: 100%;
  width: 100%;
  
}

.content-card{
  background-color: blue;
  height: 300px;
  width: 300px;
  
  margin-top: 100px;
  
  justify-content:center;
  
  display: flex;
  
  
}

.card-title{
  color: pink;
  font-size: 24pt;
  
  
}